.user-infos {
    padding-bottom: 15px;
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    /* // border-bottom: solid 1px #dedede; */
  }
  .user {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .infos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .info {
    font-size: 14px;
    margin-right: 15px;
    color: #999797;
    display: flex;
    align-items: center;
  }